import React from 'react'
import { Button } from "@/components/ui/button"

const NavigationBar = () => {
  return (
    <nav className="fixed w-full z-10 py-6">
      <div className="container mx-auto flex justify-between items-center px-4">
        <div className="text-2xl font-bold text-yellow-400">PORTFOLIO.</div>
        <div className="bg-zinc-800/90 rounded-full px-8 py-3 backdrop-blur-sm shadow-lg">
          <ul className="flex space-x-10">
            <li><Button variant="link" className="text-white hover:text-yellow-400 px-1 py-0 h-auto">Home</Button></li>
            <li><Button variant="link" className="text-white hover:text-yellow-400 px-1 py-0 h-auto">About</Button></li>
            <li><Button variant="link" className="text-white hover:text-yellow-400 px-1 py-0 h-auto">Works</Button></li>
            <li><Button variant="link" className="text-white hover:text-yellow-400 px-1 py-0 h-auto">Achievements</Button></li>
            <li><Button variant="link" className="text-white hover:text-yellow-400 px-1 py-0 h-auto">Contact</Button></li>
          </ul>
        </div>
        <div className="text-sm text-white font-medium">V1.2</div>
      </div>
    </nav>
  )
}

export default NavigationBar